#about{
    display: flex;
    flex-direction:row;
    border:1px solid red;
    
   
}
#about>div{
    width: 50%;
    border:1px solid red
}
#about>div:nth-child(1)>img{
    width: 300px ;
    height: 300px;
    margin: auto;
}

@media all and (min-width:150px ) and (max-width:580px){
    #about{
    
        flex-direction:column;
    }
    #about>div{
        width: 100%;
      
    }
 
    #about>div:nth-child(1)>img{
        width: 200px ;
        height: 200px;
        margin: auto;
    }
    
}